import React, { useState } from 'react';
import styles from './HashtagInput.module.scss';

const HashtagInput: React.FC<{ value: string[]; onChange: (value: string[]) => void; className?: string }> = ({
                                                                                                                  value,
                                                                                                                  onChange,
                                                                                                                  className = '',
                                                                                                              }) => {
    const [inputValue, setInputValue] = useState('');

    const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        let newValue = e.target.value;

        //  #    
        if (newValue.length > 0 && newValue[newValue.length - 1] === ' ') {
            const words = newValue
                .split(' ')
                .map((word) => (word && !word.startsWith('#') ? `#${word}` : word)) //  #   
                .join(' '); //     
            setInputValue(words);
        } else {
            setInputValue(newValue);
        }
    };

    const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
        if (e.key === 'Enter' && inputValue.trim()) {
            e.preventDefault();
            const newTags = inputValue
                .trim()
                .split(' ')
                .filter(Boolean)
                .map((tag) => (tag.startsWith('#') ? tag : `#${tag}`)); // ,      #
            onChange([...value, ...newTags]);
        }
    };

    const handleBlur = () => {
        if (inputValue.trim()) {
            const newTags = inputValue
                .trim()
                .split(' ')
                .filter(Boolean)
                .map((tag) => (tag.startsWith('#') ? tag : `#${tag}`));

            onChange([...value, ...newTags]);
            setInputValue(''); //   
        }
    };

    return (
        <div className={`${styles['hashtag-input']} ${className}`}>
            <label></label>
            <input
                type="text"
                value={inputValue}
                onChange={handleInputChange}
                onKeyDown={handleKeyDown}
                onBlur={handleBlur}
                placeholder="     5-8 "
            />
        </div>
    );
};

export default HashtagInput;
